@font-face {
  font-family: "Open Sans";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("/source/fonts/opensans.woff") format("woff"), url("/source/fonts/opensans.woff2") format("woff2");
}
@font-face {
  font-family: "Open Sans";
  font-weight: 300;
  font-style: normal;
  font-display: swap;
  src: url("/source/fonts/opensanslight.woff") format("woff"), url("/source/fonts/opensanslight.woff2") format("woff2");
}
@font-face {
  font-family: "Open Sans";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url("/source/fonts/opensansbold.woff") format("woff"), url("/source/fonts/opensansbold.woff2") format("woff2");
}
/* stylelint-disable function-url-quotes */
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
img {
  max-width: 100%;
  height: auto;
}
a {
  text-decoration: none;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: no-wrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page__body {
  font-family: "Open Sans", "Helvetica Neue", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 30px;
  color: #283645;
  min-height: 100%;
  margin: 0;
  background-color: #FFFFFF;
}
.page-main {
  position: relative;
}
.button {
  font-family: inherit;
  padding: 0;
  border: none;
  background-color: transparent;
}
.slider-toggle {
  padding: 0;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background-color: #283645;
  opacity: 0.3;
  cursor: pointer;
}
.slider-toggle:hover {
  opacity: 0.6;
}
.slider-toggle:active {
  opacity: 0.1;
}
.slider-toggle--current {
  padding: 0;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  border: 3px solid #283645;
  background-color: transparent;
  cursor: pointer;
}
.header {
  background-color: rgba(0, 0, 0, 0.3);
}
.page-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.page-header__logo-wrapper {
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 66px;
  align-items: center;
}
.page-header__logo {
  padding-left: 5%;
  fill: #FFFFFF;
  cursor: pointer;
}
.page-header__logo:hover .path__white {
  fill: #D22856;
}
.page-header__logo:active .path__white {
  fill: #FFFFFF;
  opacity: 0.3;
}
.logo--mobile {
  display: block;
  width: 74px;
  height: 23px;
}
.logo--tablet,
.logo--desktop {
  display: none;
}
.main-nav__item {
  cursor: pointer;
}
.main-nav__item:hover .main-nav__link {
  color: #D22856;
}
.main-nav__item:active .main-nav__link {
  opacity: 0.3;
}
.main-nav__link {
  color: #FFFFFF;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 18px;
  line-height: 32px;
}
.main-nav--closed .main-nav__toggle--opened {
  display: none;
}
.main-nav--opened .main-nav__toggle--closed {
  display: none;
}
@media (max-width: 1199px) {
  .main-nav {
    top: 66px;
    background-color: #283645;
    width: 100%;
  }
  .main-nav--closed .main-nav__items {
    display: none;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--nojs .main-nav__items {
    position: static;
  }
  .main-nav__toggle {
    position: absolute;
    width: 50px;
    height: 24px;
    top: 20px;
    right: 20px;
    background-color: transparent;
    padding: 0;
    border: none;
  }
  .main-nav__toggle:hover path {
    fill: #D22856;
  }
  .main-nav__toggle:active path {
    fill: #FFFFFF;
    opacity: 0.3;
  }
  .main-nav__items {
    display: flex;
    flex-direction: column;
    margin: 0;
    padding: 0;
    list-style: none;
  }
  .main-nav__item {
    text-align: center;
    padding-top: 25px;
    padding-bottom: 25px;
    border-top: 1px solid rgba(255, 255, 255, 0.15);
    border-bottom: 1px solid rgba(0, 0, 0);
  }
}
@media (min-width: 660px) {
  .logo--mobile {
    display: none;
  }
  .logo--tablet {
    display: block;
    width: 145px;
    height: 40px;
  }
}
@media (min-width: 1200px) {
  .logo--tablet {
    display: none;
  }
  .main-nav__toggle {
    display: none;
  }
  .logo--desktop {
    display: block;
    width: 147px;
    margin-left: 40%;
  }
  .logo--desktop:active .path__white {
    fill: #FFFFFF;
    opacity: 0.3;
  }
  .logo--desktop:active .path__pink {
    fill: #D22856;
    opacity: 0.3;
  }
  .page-header {
    height: 110px;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  .main-nav {
    margin-right: 10%;
  }
  .main-nav__items {
    width: 680px;
    display: flex;
    justify-content: space-between;
  }
  .main-nav__item {
    position: relative;
    padding: 20px 30px;
  }
  .main-nav__link {
    text-transform: none;
    font-weight: 400;
    font-size: 18px;
    line-height: 18px;
  }
  .main-nav__link--current::after {
    position: absolute;
    content: "";
    width: 70%;
    height: 2px;
    background-color: #FFFFFF;
    left: 20px;
    bottom: -20px;
  }
}
