    display: none;
  }
  .reviews {
    position: relative;
    padding: 15% 5%;
    /*delete*/
    outline: 5px solid rgba(0, 255, 255, 0.3);
    outline-offset: -5px;
  }
  .reviews::before {
    position: absolute;
    content: "";
    width: 79px;
    height: 63px;
    top: 20%;
    left: 48%;
    background-image: url("../img/content/index/quotes.svg");
    background-repeat: no-repeat;
  }
  .review__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 50px;
  }
  .review__button {
    position: relative;
    display: block;
    width: 50px;
    height: 50px;
    border: none;
    background-color: transparent;
    cursor: pointer;
  }
  .review__previous::before {
    position: absolute;
    content: "";
    border: 2px solid rgba(40, 54, 69, 0.3);
    top: 15%;
    left: 40%;
    rotate: 45deg;
    width: 29px;
    height: 29px;
    border-width: 0 0 2px 2px;
  }
  .review__previous:hover::before {
    position: absolute;
    content: "";
    border: 2px solid rgba(40, 54, 69, 0.6);
    top: 15%;
    left: 40%;
    rotate: 45deg;
    width: 29px;
    height: 29px;
    border-width: 0 0 2px 2px;
  }
  .review__previous:active::before {
    position: absolute;
    content: "";
    border: 2px solid rgba(40, 54, 69, 0.1);
    top: 15%;
    left: 40%;
    rotate: 45deg;
    width: 29px;
    height: 29px;
    border-width: 0 0 2px 2px;
  }
  .review__next::before {
    position: absolute;
    content: "";
    border: 2px solid rgba(40, 54, 69, 0.3);
    top: 15%;
    left: -2%;
    rotate: 45deg;
    width: 29px;
    height: 29px;
    border-width: 2px 2px 0 0;
  }
  .review__next:hover::before {
    position: absolute;
    content: "";
    border: 2px solid rgba(40, 54, 69, 0.6);
    top: 15%;
    left: -2%;
    rotate: 45deg;
    width: 29px;
    height: 29px;
    border-width: 2px 2px 0 0;
  }
  .review__next:active::before {
