    height: 32px;
  }
}
input[type="range"] {
  width: 240px;
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
}
input[type="range"]::-webkit-slider-thumb {
  width: 14px;
  height: 14px;
  border: 2px solid #D22856;
  border-radius: 50%;
  background-color: #FFFFFF;
  cursor: pointer;
  margin-top: -6px;
  -webkit-appearance: none;
}
input[type="range"]::-webkit-slider-thumb:active {
  background-color: #D22856;
}
input[type="range"]::-webkit-slider-runnable-track {
  height: 2px;
  background-color: #283645;
  border: none;
}
@media (min-width: 660px) {
  input[type="range"] {
    width: 202px;
    background-color: transparent;
  }
}
@media (min-width: 1200px) {
  input[type="range"] {
    width: 288px;
  }
}
.editor__button-wrapper {
  display: flex;
  flex-direction: column;
  margin-top: 32px;
}
@media (min-width: 660px) {
  .editor__button-wrapper {
    margin: 0;
  }
}
@media (min-width: 1200px) {
  .editor__button-wrapper {
    flex-direction: row;
    justify-content: space-between;
    width: 410px;
  }
}
.editor__button {
  font-weight: 700;
  font-size: 18px;
  line-height: 18px;
  width: 280px;
  border: 3px solid;
  border-radius: 30px;
  margin: 6px auto;
  padding: 21px 0;
}
@media (min-width: 660px) {
  .editor__button {
    width: 260px;
    margin-top: 24px;
  }
}
@media (min-width: 1200px) {
  .editor__button {
    width: 183px;
    margin: 0;
  }
}
.editor__button--submit {
  color: #D22856;
}
.editor__button--submit:hover {
  background-color: #D22856;
}
.editor__button--submit:hover .submit-text {
  color: #FFFFFF;
}
.editor__button--submit:active .submit-text {
  opacity: 0.3;
}
.editor__button--reset {
  color: #283645;
  opacity: 0.1;
}
.editor__button--reset:hover {
  opacity: 0.3;
}
.editor__button--reset:active {
  background-color: #283645;
  opacity: 0.3;
}
.editor__button--reset:active .reset-text {
  color: #FFFFFF;
  opacity: 0.3;
}
@media (max-width: 659px) {
  .editor__icon path {
    cursor: pointer;
    fill: #283645;
    opacity: 0.3;
  }
  .editor__icon:hover path {
    opacity: 0.5;
  }
  .editor__icon:active path {
    fill: #D22856;
  }
  .editor__icon--current-mobile path {
    fill: #D22856;
    opacity: 1;
  }
}
